import './App.css';
import React from 'react';
import ReactDOM from 'react-dom/client';
import  {evaluate} from 'mathjs'
import { create, all } from 'mathjs'
import update from 'immutability-helper'
const config = { };
const math = create(all, config);
class App extends React.Component{
  constructor(props){
    super(props);
    this.state={
      operations:[],
    }
    this.handleClick=this.handleClick.bind(this);
    this.count=this.count.bind(this);
  }
  count(){
    let result = this.state.operations.join('')
    if (result) {
      result = math.evaluate(result)
      result = math.format(result, { precision: 14 })
      result = String(result)
      this.setState({
        operations: [result],
      })
    }
  }

  handleClick(event){
    switch (event.target.className) {
      case "Clear":this.setState({operations:[]}); break;
      case "Equals":this.count();break;
      default:const newOperations=update(this.state.operations, {$push:[event.target.value]}); this.setState({operations:newOperations}); console.log(this.state.operations);
        break;

    }
        console.log("yay!");
        console.log(this.state.operations);

  }
  render(){
    return(
      <div className="Container">
          <div className="InputWindow">
              <div className="InputIsHere" >
              {this.state.operations}

              </div>
          </div>
          <div className="Buttons">
                  <button className="Opers" value="+" onClick={this.handleClick}>+</button>
                  <button className="Opers" value="-"  onClick={this.handleClick}>-</button>
                  <button className="Opers" value="*"  onClick={this.handleClick}>*</button>
                  <button className="Opers" value="/" onClick={this.handleClick}>/</button>
                  <button className="Numbers" value="7"  onClick={this.handleClick}>7</button>
                  <button className="Numbers" value="8"  onClick={this.handleClick}>8</button>
                  <button className="Numbers" value="9"  onClick={this.handleClick}>9</button>
                  <button className="Equals"  onClick={this.handleClick}>=</button>
                  <button className="Numbers" value="4"  onClick={this.handleClick}>4</button>
                  <button className="Numbers" value="5"  onClick={this.handleClick}>5</button>
                  <button className="Numbers" value="6"  onClick={this.handleClick}>6</button>
                  <button id="id1" className="Numbers" value="1"  onClick={this.handleClick}>1</button>
                  <button className="Numbers" value="2"  onClick={this.handleClick}>2</button>
                  <button className="Numbers"  value="3" onClick={this.handleClick}>3</button>
                  <button id="id2" className="Clear"  onClick={this.handleClick}>AC</button>
                  <button  className="Numbers" value="0" id="id0" onClick={this.handleClick}>0</button>
          </div>
      </div>
    )
  }

}

export default App;
